@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
:root{--color-very-dark-gray: hsl(0, 0%, 17%);--color-dark-gray: hsl(0, 0%, 59%);--color-white: hsl(0, 0%, 95%);--color-dark: hsl(0, 0%, 0%);--font-family: "Rubik", sans-serif;font-size:18px;--font-size-title: 1.5em;--font-size-item-title: 0.6em;--font-weight-regular: 400;--font-weight-medium: 500;--font-weight-bold: 700}html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;box-sizing:border-box}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1;overflow-x:hidden;font-family:"Rubik", sans-serif;font-family:var(--font-family)}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}main{display:flex;flex-direction:column;height:100vh}.map{flex:1 1;min-height:90vh;width:100vw}.banner{background-image:url("https://res.cloudinary.com/dmfhnhyyj/image/upload/v1621845490/Ip%20Tracker/pattern-bg_zn9xws.png");background-position:50% 0%;display:flex;flex-direction:column;height:30vh;padding:32px 24px;width:100vw}.banner__form{display:flex;margin-top:1.6em;align-items:center}.banner__title{color:hsl(0, 0%, 95%);color:var(--color-white);font-size:1.5em;font-size:var(--font-size-title);font-weight:500;font-weight:var(--font-weight-medium);text-align:center}.banner__input{border:none;border-radius:1em 0 0 1em;color:hsl(0, 0%, 17%);color:var(--color-very-dark-gray);font-family:"Rubik", sans-serif;font-family:var(--font-family);font-size:1em;font-weight:400;font-weight:var(--font-weight-regular);flex:1 1;padding:24px}.banner__button{background-color:hsl(0, 0%, 0%);background-color:var(--color-dark);border:none;cursor:pointer;height:69px;width:69px;border-radius:0 1em 1em 0}.banner__button:active{opacity:.9}.banner__data-container{background-color:hsl(0, 0%, 95%);background-color:var(--color-white);border-radius:1em;box-shadow:1px 8px 15px 0px rgba(0,0,0,.61);display:flex;flex-direction:column;margin-top:24px;padding:24px;position:relative;z-index:10}.banner__data-item{display:flex;flex-direction:column}.data-item__title{color:hsl(0, 0%, 59%);color:var(--color-dark-gray);font-size:0.6em;font-size:var(--font-size-item-title);font-weight:700;font-weight:var(--font-weight-bold);letter-spacing:.2em;text-align:center;width:100%}.data-item__data{color:hsl(0, 0%, 17%);color:var(--color-very-dark-gray);font-size:1em;font-weight:500;font-weight:var(--font-weight-medium);padding:1em;text-align:center}@media(min-width: 960px){.banner{align-items:center;padding:24px 64px;height:30vh;width:100vw}.banner__form{align-items:center;display:flex;margin-top:1.6em;width:40%}.banner__title{color:hsl(0, 0%, 95%);color:var(--color-white);font-size:1.5em;font-size:var(--font-size-title);font-weight:500;font-weight:var(--font-weight-medium);text-align:center}.banner__input{border:none;border-radius:1em 0 0 1em;color:hsl(0, 0%, 17%);color:var(--color-very-dark-gray);font-family:"Rubik", sans-serif;font-family:var(--font-family);font-size:.8em;font-weight:400;font-weight:var(--font-weight-regular);flex:1 1;padding:24px}.banner__button{background-color:hsl(0, 0%, 0%);background-color:var(--color-dark);border:none;cursor:pointer;height:69px;width:69px;border-radius:0 1em 1em 0}.banner__button:active{opacity:.9}.banner__data-container{align-items:center;background-color:hsl(0, 0%, 95%);background-color:var(--color-white);border-radius:1em;box-shadow:1px 8px 15px 0px rgba(0,0,0,.61);display:flex;justify-content:space-around;flex-direction:row;margin-top:24px;padding:24px;position:relative;min-width:60%;z-index:10}.banner__data-item{display:flex;flex-direction:column}.data-item__title{color:hsl(0, 0%, 59%);color:var(--color-dark-gray);font-size:0.6em;font-size:var(--font-size-item-title);font-weight:700;font-weight:var(--font-weight-bold);letter-spacing:.2em;text-align:center;width:100%}.data-item__data{color:hsl(0, 0%, 17%);color:var(--color-very-dark-gray);font-size:1em;font-weight:500;font-weight:var(--font-weight-medium);padding:1em;text-align:center}}
